import React, { Component } from "react";
import { Button, Row, Col ,Card} from "reactstrap";
import "bootstrap/dist/css/bootstrap.css";
import "../styles.css";

import Input from "./common/input";
import Joi from "joi-browser";

function validateEmail(email) {
  const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  return re.test(String(email).toLowerCase());
}

const postNewUser = (name, email) => {
  console.log("do fetch");
  fetch(
    "https://vipclub.customerboomerang.com/version-test/api/1.1/wf/addCustomerApi",
    {
      method: "POST",
      body: JSON.stringify({
        Name: name,
        Email: email,
        AccountShortName: "KanTralee"
      }),
      headers: {
        "Content-type": "application/json; charset=UTF-8"
      }
    }
  )
    .then((response) => response.json())
    .then((json) => console.log(json));
};

class SignupForm extends Component {
  state = {
    userDetails: { name: "", email: "" },
    errors: {}
  };

  schema = {
    name: Joi.string().required(),
    email: Joi.string().required()
  };

  renderInput = (name, label) => {
    const userDetails = { ...this.state.userDetails };
    const errors = { ...this.state.errors };

    return (
      <Input
        name={name}
        value={userDetails[name]}
        label={label}
        onChange={this.handleChange}
        error={errors[name]}
      />
    );
  };

  validate = () => {
    console.log(this.state.userDetails.name);
    const newErrors = {};

    if (this.state.userDetails.name.trim() === "") {
      newErrors.name = "Name Cannot be Blank";
    }

    if (!validateEmail(this.state.userDetails.email)) {
      newErrors.email = "Invalid Email ";
    }

    console.log(newErrors);

    return newErrors;
  };

  handleSubmit = (e) => {
    e.preventDefault();

    const errors = this.validate();

    this.setState({ errors: errors || {} });

    postNewUser(this.state.userDetails.name, this.state.userDetails.email);

    if (errors) return;
  };

  validateProperty = ({ name, value }) => {
    if (name === "name") {
      if (value.trim() === "") return "user name required";
    }

    if (name === "email") {
      if (value.trim() === "") return "email required";
    }

    return "";
  };

  handleChange = ({ currentTarget: input }) => {
    const errors = { ...this.state.errors };

    const errorMessage = this.validateProperty(input);
    if (errorMessage) errors[input.name] = errorMessage;
    else delete errors[input.name];

    const userDetails = { ...this.state.userDetails };
    userDetails[input.name] = input.value;

    this.setState({ userDetails, errors });
  };

  render() {
    const { userDetails, errors } = this.state;

    return (
      <div className="signupform">
        <h2> Form header </h2>
        <form onSubmit={this.handleSubmit}>
          {this.renderInput("name", "Name")}
          {this.renderInput("email", "Email")}

          <button className="btn btn-primary ">Submit</button>
        </form>
      </div>
    );
  }
}

export default SignupForm;
