import React, { Component } from "react";
import { Container, Button, Row, Col, Card } from "reactstrap";
import "../styles.css";
import Menus from "../Data/Menu.jsx";

import "bootstrap/dist/css/bootstrap.css";

const renderMenu = (menu) => {
  console.log("Menu", menu);
  console.log("items", menu.items);

  const newMenu = menu.items.map((item) => {
    return renderMenuItem(item);
  });
  console.log("nEW", newMenu);

  return (
    <div>
      <h1> {menu.Section} </h1>
      {newMenu}
    </div>
  );
};

const renderMenuItem = (item) => {
  return (
    <Row key={item.name}>
      <Col>
        <h1 className="menuItemHeader"> {item.name} </h1>
        <div className="menuItemDescription"> {item.description} </div>
        <div className="allergen"> {item.allergens}</div>
      </Col>

      <Col>5.55</Col>
    </Row>
  );
};

const renderImage = (image) => {
  return <img src={image} fluid alt="" className="menuImage" />;
};

class MenuDisplay extends Component {
  render() {
    const fullMenu = Menus.map((menuSection, i) => {
      return (
        <Row className="menuSection">
          {i % 2 === 0 ? (
            <Col md={6}>{renderMenu(menuSection)}</Col>
          ) : (
            <Col md={6}>{renderImage(menuSection.sectionImage)}</Col>
          )}

          {i % 2 === 0 ? (
            <Col md={6}>{renderImage(menuSection.sectionImage)}</Col>
          ) : (
            <Col md={6}>{renderMenu(menuSection)}</Col>
          )}
        </Row>
      );
    });

    return fullMenu;
  }
}

export default MenuDisplay;
